import React from "react"
import DarkMode from "./DarkModeBtn"
import { Link } from "gatsby"

const Layout = ({ location, title, children }) => {
  const rootPath = `${__PATH_PREFIX__}/`
  const isRootPath = location.pathname === rootPath
  let header

  if (isRootPath) {
    header = (
      <h1 className="main-heading">
        <Link to="/">{title}</Link>
      </h1>
    )
  } else {
    header = (
      <Link className="header-link-home" to="/">
        {title}
      </Link>
    )
  }

  return (
    <div className="global-wrapper" data-is-root-path={isRootPath}>
      <header className="global-header" style={{ display: "flex", justifyContent: "space-between" }}>
        {header}
        <DarkMode />
      </header>
      <main>{children}</main>
      <footer>
        <a href="https://github.com/HolyZheng">Github</a>
        {`  `}
        <a href="https://juejin.cn/user/2875978147699534/posts"></a>
      </footer>
    </div>
  )
}

export default Layout
